.img-info-call{
    background-image: url(../../../assets/img/call.png);
    bottom: 3rem;
    width: 95%;
    height: 23rem;
    background-size: cover;
    backface-visibility: unset;
    background-repeat: unset;
    position: relative;
    left: 2rem;
}
.box-one-text{
    background-color: rgb(33, 32, 32);
    display: flex;
    width: 10%;
    justify-content: center;
    padding: 12px;
    position: relative;
    top: 12rem;
    filter: blur(0.5px);
    left: 43rem;
    font-family: 'IranianSansBold';
    font-size: 20px;
    text-align: center;align-self: center;border-radius: 10px;

}
