@tailwind base;
@tailwind components;
@tailwind utilities;


@font-face {
	font-family: IranianSansBold;
	src: url('../src/assets/font/IranianSansBold.ttf') format('truetype');
	font-weight: normal;
	font-style: normal;
}

/* END Bold */
/* BEGIN Regular */
@font-face {
	font-family: 'IranianSans';
	src: url('../src/assets/font/IranianSansRegular.ttf') format('truetype');
	font-weight: normal;
	font-style: normal;
	font-family: 'Mj Bold';
	src: ('../public/Mj Bold.ttf');
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a,li,ul{
  list-style-type: none;
}
::-webkit-scrollbar{
	width: 14px;
}
::-webkit-scrollbar-thumb{
	background-color: #3B71FE;
}

