
@media screen and (max-width: 500px) {

    ::-webkit-scrollbar{
    }
    ::-webkit-scrollbar-thumb{
        background-color: #3B71FE;
    }
}
@media screen and (max-width: 600px) {
    #menu-phone{display: none;}


        ::-webkit-scrollbar{
        }
        ::-webkit-scrollbar-thumb{
            background-color: #3B71FE;
        }
        
    
    /* استایل‌های مخصوص گوشی‌های هوشمند */

    #header-mobile{
        display: flex;
        justify-content: end;
        left: 0px;
        height: 5rem;
        border-radius: 1px;
        bottom: 0rem;
        width: 100%;
        top: 0rem;
    }
    .header-text{
        display: none;
    }
    #buHeader{
        display: none;
    }
    #menu-phone{cursor: pointer;}
    .menu-bar{
        display: flex;
        position: relative;
        text-align: center;
        align-items: center;
    }
    .menu-logo{
        display: flex;
        justify-content: space-between;
        width: 100%;
        padding: 12.5px;
        margin: 10px;
        height: auto;
        text-align: center;
        align-items: center;
    }
    
    

}
@media screen and (min-width: 601px) and (max-width: 1024px) {
    /* استایل‌های مخصوص تبلت‌ها */
    #menu-none{display: none;}
    .icon-down{display: none;}
    .call-info{display: none;}
    .box-two-text {display: none;}
    .wiper-slide {display: none;}
    #header-width{width: 33rem; display: flex;justify-content: end;}
    #header-gao-mobile{gap: 23px; display: flex; justify-content: center; align-items: center; text-align: center;}
    #header-mobile{width: 100%; left: 0rem; bottom: 0rem;border: 0px solid #0000; border-radius: 10px; height: 4.5rem;}
    #call-moboile{display: block;}
    .menu-logo{display: none;}
    #buHeader{justify-content: center; text-align: center; align-items: center; gap: 0px;} 
    .wiper-slide {display: none;}
}
@media screen and (min-width: 1025px) {
    /* استایل‌های مخصوص کامپیوترها */
    .header{
        display: block;
    }
    .header-text{
        display: block;
    }
    .menu-bar{
        display: none;
    }
    .menu-logo{
        display: none;
    }
    #header-mobile{
    width: 100%;
    margin-left: 5rem;
    }
    #buHeader{
        width: 900%;
    }
    
}

