.container {
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
    top: 4rem;
    position: relative;
    flex-direction: column;
    bottom: 5rem;
    top: 0;
    width: 17rem;
}

.wrapper {
    position: relative;
    bottom: 0;
    top: -6rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.num {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: x-large;


}

.text {
    font-size: medium;
    font-weight: 700;
    margin: 5px;
}

.text-sum {
    color: blue;
    font-size: x-large;
    position: relative;
    left: 0.5rem;
}

