@media screen and (max-width: 600px) {

    /* box-moblie-style.css */
    .slick-prev,
    .slick-next {
        color: #3B71FE;
        /* Change this to your desired color */
    }

    .slick-prev:before,
    .slick-next:before {
        color: #3B71FE;
        /* Change this to your desired color */
    }

    .slider-container img {
        border-radius: 15px;
        /* Adjust the value as needed */
        position: relative;
    }

    .slider-container {
        position: relative;
        bottom: 4rem;
        cursor: ew-resize;
        border-radius: 15px;
        /* Adjust the value as needed */
        border: 1px solid #ffffff;
        /* Customize the border style as needed */

    }

    .active-slide {
        border: 1px solid #ffffff;
        /* Customize the border style as needed */
    }

    #slider-box {
        align-items: center;
        width: 100%;
        display: inline-block;
        text-align: center;
        justify-content: center;
        display: inline-flex;
    }

    .slick-slide {
        display: flex;
        justify-content: center;
    }
}

@media screen and (min-width: 601px) and (max-width: 1024px) {
    .slider-container {
        display: none;
    }
}

@media screen and (min-width: 1025px) {
    .slider-container {
        display: none;
    }

}