.img-header{
    width: 100%;
    height: 46rem;
    background-size: cover;
    backface-visibility: unset;
    background-repeat: unset;
    position: relative;
    background-position: center;
    background-image: url(../../assets//img/backgruod-Home.png);
}
.text-header-Home{
    text-align: center;
    font-size: large;
}
.text-two{
    font-size: 50px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
@media screen and (max-width: 600px) {
    .text-mobile{
        position: relative;
        bottom: 6rem;
    }

    .text-two{
        position: relative;
        font-size: 25px;
        font-family: 'IranianSansBold';
        bottom: 4.5rem;
    }
}
