.header-wiper {
    display: flex;
    width: 100%;
    justify-content: space-around;
}




.wiper-slide {
    border-radius: 20px;
    background-color: #F4F5F6;
    position: relative;
    text-align: center;
    height: 238px;
    width: 237px;
}

.warranty {
    justify-content: end;
    display: flex;
    width: 15rem;
    padding: 12px;
}

.text-logo {
    display: flex;
    justify-content: right;
    padding: 11px;
}

.text-Description {

    display: flex;
    width: 15rem;
    font-size: 16px;
    font-stretch: semi-condensed;
    font-family: system-ui;
    text-align: center;
    align-items: baseline;
    top: 2rem;
    position: relative;
}

@media screen and (max-width: 300px) {
 
    .wiper-slide {
        flex: 0 0 auto;
        scroll-snap-align: start;
        width: 100%;
        box-sizing: border-box;
        padding: 10px;
        text-align: center;
    }

    .wiper-slide {
        display: block;
        width: 100%;
    }

    #wiper-on {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 10px;
        /* فاصله بین باکس‌ها */
    }

    #wiper-on:hover {
        background: linear-gradient(135deg, #00f, #fff, #0f0);
        background-size: 400% 400%;
        animation: wave 5s ease infinite;
        color: black;
    }

    @keyframes wave {
        0% {
            background-position: 0% 50%;
        }

        50% {
            background-position: 100% 50%;
        }

        100% {
            background-position: 0% 50%;
        }
    }


    .wiper-slide {
        display: none;
    }

}

@media screen and (max-width: 600px) {


    .text-logo {
        display: flex;
        justify-content: center;
        padding: 0px;
        width: 12rem;
        height: 4rem;
        text-align: center;
        align-items: center;
    }

    
    .warranty{
        top: -2rem;
        position: relative;
        display: flex;
        height: 4rem;
        width: 5rem;
        right: 2rem;
    }

    .text-Description{
        display: none;
    }
    .header-wiper {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        gap: 4px;
    }

    .wiper-slide {}

    #wiper-on {
        display: flex;
        gap: 20px; 
        flex-direction: row;
        background-color: lightgray;
    }

    #wiper-on:hover {
        background: linear-gradient(135deg, #00f, #fff, #0f0);
        background-size: 400% 400%;
        animation: wave 5s ease infinite;
        color: black;
    }

    @keyframes wave {
        0% {
            background-position: 0% 50%;
        }

        50% {
            background-position: 100% 50%;
        }

        100% {
            background-position: 0% 50%;
        }
    }
    .warranty {
        display: none;

    }

    .wiper-slide {
        display: none;
    }
    #text-Description{display: block;}

}