.info-org {
    display: flex;
    justify-content: space-between;
    text-align: center;
    align-items: center;
    width: 100%;
    height: auto;
}

.info-img {
    position: relative;
    width: 80%;
    height: 27rem;
    left: 4rem;
}

.info-text {
    right: 16px;
    font-variant: all-small-caps;
    display: flex;
    flex-direction: row;
    width: 75%;
    text-align: justify;
    position: relative;
    font-family: "IranianSansBold";
    direction: rtl;
    filter: blur(0.5px);

}

.text-but {
    display: flex;
    width: 41rem;
    position: relative;
    bottom: 4.5rem;
    right: 12rem;
    font-family: 'IranianSansBold';
    font-size: 35px;
    color: blue;
    font-weight: 500;
    height: auto;
}

.icon-down {
    top: 6rem;
    background-image: url(data:image/png;base64,);
    width: 2rem;
    height: 30px;
    background-size: cover;
    left: 79rem;
    backface-visibility: unset;
    background-repeat: unset;
    position: relative;
    background-position: center;
    text-align: center;
    align-items: center;
    display: flex;
    border-radius: 10px;
}


@media screen and (max-width: 600px) {
    .icon-down {
        display: none;
    }

    .text-but {
        display: none;
    }

    .info-img {
        display: none;
    }

    .info-text {
        text-align: center;
        display: none;
    }
}




@media screen and (min-width: 601px) and (max-width: 1024px) {
    .icon-down {
        display: none;
    }

}
@media screen and (min-width: 1025px) {
    .slider-container {
        display: block;
    }

}